<template>
  <transition name="slide">
    <div class="user-center">
      <div class="title">
        <div class="back-icon" @click="back">
          <i class="iconfont">&#xe662;</i>
        </div>
        <switches :switches="switches" :currentIndex="currentIndex" @switch="setCurrentIndex"></switches>
      </div>

      <div class="play-wrapper">
        <div class="play" @click="random">
          <i class="iconfont">&#xe627;</i>
          <span>随机播放全部</span>
        </div>
      </div>

      <div class="list-wrapper" ref="listWrapper">
        <scroll class="list-scroll" v-if="currentIndex === 0" :data="favoriteList" ref="favoriteList">
          <div class="list-inner">
            <song-list :songs="favoriteList" @select="selectSong"></song-list>
          </div>
        </scroll>
        <scroll class="list-scroll" v-if="currentIndex === 1" :data="playHistory" ref="playList">
          <div class="list-inner">
            <song-list :songs="playHistory" @select="selectSong"></song-list>
          </div>
        </scroll>
      </div>

      <div class="no-result-wrapper" v-show="noResult">
        <no-result :title="noResultDesc"></no-result>
      </div>

    </div>
  </transition>
</template>

<script>
import Switches from '@/base/switches/switches'
import Scroll from '@/base/scroll/scroll'
import SongList from '@/base/song-list/song-list'
import {mapGetters,mapActions} from 'vuex'
import Song from "@/common/js/song"
import {playlistMixin} from '@/common/js/mixin'
import NoResult from '@/base/no-result/no-result'
export default {
  mixins: [playlistMixin],
  data(){
    return {
      currentIndex: 0,
      switches: [
        {name:"我的收藏"},
        {name:"最近播放"}
      ]
    }
  },
  methods: {
    back(){
      this.$router.back()
    },
    setCurrentIndex(index){
      this.currentIndex = index
    },
    selectSong(song){
      this.insertSong(new Song(song))
    },
    random(){
      let list = this.currentIndex === 0 ? this.favoriteList : this.playHistory
      if (list.length === 0) {
        return
      }
      list = list.map((item)=>{
        return new Song(item)
      })
      this.randomPlay({
        list
      })
    },
    handlePlaylist(playlist) {
      const bottom = playlist.length > 0 ? '60px' : ''
      this.$refs.listWrapper.style.bottom = bottom
      this.$refs.favoriteList && this.$refs.favoriteList.refresh()
      this.$refs.playList && this.$refs.playList.refresh()
    },
    ...mapActions([
      'insertSong',
      'randomPlay'
    ])
  },
  computed: {
    noResult() {
      if (this.currentIndex === 0) {
        return !this.favoriteList.length
      } else {
        return !this.playHistory.length
      }
    },
    noResultDesc() {
      if (this.currentIndex === 0) {
        return '暂无收藏歌曲'
      } else {
        return '您最近没有听过歌曲'
      }
    },
    ...mapGetters([
      'favoriteList',
      'playHistory'
    ])
  },
  components: {
    Switches,
    Scroll,
    SongList,
    NoResult
  }
}
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~@/common/stylus/var"
  .user-center
    position: fixed
    top: 0
    bottom: 0
    z-index: 100
    width: 100%
    background: $color-background
    &.slide-enter-active, &.slide-leave-active
      transition: all 0.3s
    &.slide-enter, &.slide-leave-to
      transform translate3d(100%, 0, 0)
    .title
      margin 10px 0 30px 0
      .back-icon
        position absolute
        top 6px
        left 0px
        .iconfont
          display: block
          padding: 10px
          font-size: $font-size-large-x
          color: $color-theme-l
    .play-wrapper
      .play
        box-sizing: border-box
        width: 135px
        padding: 7px 0
        margin: 0 auto
        text-align: center
        border: 1px solid $color-theme
        color: $color-theme
        border-radius: 100px
        .icon-play
          display: inline-block
          vertical-align: middle
          margin-right: 6px
          font-size: $font-size-medium-x
        .text
          display: inline-block
          vertical-align: middle
          font-size: $font-size-small
    .list-wrapper
      position: absolute
      top: 110px
      bottom: 0
      width: 100%
      .list-scroll
        height: 100%
        overflow: hidden
        .list-inner
          padding: 20px 30px
    .no-result-wrapper
      position: absolute
      width: 100%
      top: 50%
      transform: translateY(-50%)
</style>



